import React from "react";
import "./SectionSaran.scss";
import { Button } from "../Button";

const SectionSaran = ({ onClick }) => {
  return (
    <div className="container">
      <div className="section-saran">
        <div className="content">
          <p>Lalu, bagaimana pendapatmu tentang Sitani?</p>
          <Button onClick={onClick} color="secondary" bgcolor="bgprimary">
            Saran
          </Button>
        </div>
      </div>
    </div>
  );
};
export default SectionSaran;
